.guaranteePoints > div:first-of-type {
  display: flex !important;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: stretch;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.guaranteePoints,
.guaranteePoints > div > div {
  background-color: white;
}

.guaranteePoints > div > div {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  height: 65px;
  padding: 0;
  padding-top: 8px;
}

.guaranteePoints > div > div > span {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 64px;
  text-align: center;
}
