.guarantee {
  background-color: white;
  width: 100%;
  min-height: 48px;
  padding-top: 12px;
  padding-right: 30px;
  margin-bottom: 7px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: flex-start;
  justify-content: flex-start;
  align-items: baseline;
}

.icon {
  margin-left: 12px;
  color: #05BEC0;
  fill: #05BEC0;
}


.label {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #585858;
  display: inline-block;
  max-width: 266px;
}
