.SaleDetailsForm {

}

.SaleDetailsForm .choocePriceTitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #323232;
}

.SaleDetailsForm > :global(.MuiGrid-grid-xs-12) > div {

  background-color: white;
  padding: 40px;
  margin-bottom: 25px;
}

.SaleDetailsForm :global(.GenericDetail) {
  margin-bottom: 4px;
}


.SaleDetailsForm > :global(.MuiGrid-grid-xs-12) > div span {
  // color: #FBFBFB !important;
  // display: block;
  display: inline-block;
}


.SaleDetailsForm > :global(.MuiGrid-grid-xs-12) > div :global(.title) {
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #323232;
}

@media (max-width: 500px) {
  .SaleDetailsForm > :global(.MuiGrid-grid-xs-12) > div :global(.title) {
    font-size: 17px;
    line-height: 20px;
  }
}
