.PriceSelector {
  background-color: white;
  padding-top: 100px;
  padding-bottom: 54px;
  -webkit-user-select: unset; /* Safari */
  -webkit-touch-callout: unset; /* iOS Safari */
  -khtml-user-select: unset; /* Konqueror HTML */
  -ms-user-select: unset; /* Internet Explorer/Edge */
  -moz-user-select: unset; /* Old versions of Firefox */
  user-select: unset; /* Non-prefixed version*/
  width: 100%;
  background-image: url(/assets/img/loginBackground.svg);
  -webkit-background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  -o-background-size: 100% 100%;
  background-size: 100% 100%;
}


.innerPriceSelector {
  max-width: 495px;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffffb8;
}

.PriceSelector.error {
  border-width: 1px;
  color: #c13515 !important;
  box-shadow: #c13515 0px 0px 0px 1px inset !important;
  background-color: #fff8f6 !important;
  border-color: #c13515 !important;
}


.PriceSelector p.error {
  color: #c13515 !important;
  border-color: transparent;
  position: absolute;
  margin-right: 62px;
}

@media (max-width: 767px) {
  .PriceSelector {
    padding-left: 18px;
    padding-right: 18px;
  }
}

@media (max-width: 360px) {
  .PriceSelector {
    padding-top: 65px;
    padding-bottom: 34px;
  }
}
