.mainControlContainer {
  max-width: 1110px;
  margin-left: auto;
  margin-right: auto;
  min-height: 40vw;
  padding-top: 30px;
  padding-bottom: 30px;
}

main.mainContainer :global(h2.topRow) {
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;

  color: #585858;

}


@media (max-width: 767px) {
  .mainControlContainer {
    padding-top: 0px;
    padding-bottom: 0px;
  }

}
