.ChooseEditorTypeButton,
.ChooseEditorTypeButton:hover {
  display: inline-block;
  background-color: #FFFFFF;
  filter: drop-shadow(0px 2px 2px rgba(218, 218, 218, 0.25));
  border-radius: 108px;
  min-width: 178.31px;
  height: 47px;
  //  margin: 0 auto;
  text-align: center;
  padding: 10px 10px;
  position: relative;
  top: -8px;

  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #05BEC0;
  margin-bottom: 25px !important;
}

.ChooseEditorTypeButton .label {
  margin-right: 15px;
}

.ChooseEditorTypeButton svg {
  font-size: 24px;
  margin-right: 8px;
  margin-top: -5px;
  margin-left: 15px;
}

.ChooseEditorTypeButton:global(.active),
.ChooseEditorTypeButton:global(.active):hover {
  background-color: #05BEC0;
  color: #FFFFFF;
}

@media (max-width: 610px) {
  .ChooseEditorTypeButton,
  .ChooseEditorTypeButton:hover {
    overflow: initial;
    min-width: 60px;
    width: 60px;
    height: 60px;
  }

  .ChooseEditorTypeButton svg,
  .ChooseEditorTypeButton .label {
    color: #878787;
  }

  .ChooseEditorTypeButton svg {
    margin-top: 4px;
    margin-left: auto;
    margin-right: auto;
    font-size: 32px;
  }

  .ChooseEditorTypeButton:global(.active) svg {
    color: white;
  }

  .ChooseEditorTypeButton .label {
    margin: 0;
    width: 100%;
    position: absolute;
    top: 65px;
    right: 0;
    font-weight: normal;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
  }

  .ChooseEditorTypeButton:global(.active) .label {
    color: #05BEC0;
  }

}
