.PriceRow {
  width: 100%;
  height: fit-content;
  margin-bottom: 24px;
  overflow: hidden;
}

.PriceRow > .label {
  float: right;
  font-size: 18px;
  line-height: 50px;
  text-align: start;
  color: #585858;
  display: inline-block;

}

.PriceRow > .value {
  // background-color: transparent;
  // border: 0px solid #BEBEBE;
  // box-sizing: border-box;
  //  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.05);
  //  border-radius: 4px;

  width: 94px;
  float: left;
  font-size: 18px;
  line-height: 48px;
  text-align: center;
  color: #323232;
  display: inline-block;
}

.PriceRow > .value.input,
.PriceRow > .value.input :global(.MuiTextField-root),
.PriceRow > .value.input fieldset {
//  border: 0;
//  border-bottom: 1px;
//  border-bottom-width: 1px;
//  border-bottom-style: solid;
//  border-radius: 0;
}

.PriceRow.conclusion > .label,
.PriceRow.conclusion > .value {
  font-weight: bold;
}


.PriceRow .value:global(.MuiTextField-root),
.PriceRow .value:global(.MuiTextField-root) > :global(.MuiInputBase-root) {
  padding: 0;
  height: 49px;
}

.PriceRow .value:global(.MuiTextField-root) > :global(.MuiInputBase-root) :global(input.MuiInputBase-input) {
  padding-right: 0;
  padding-top: 0;
  padding-bottom: 0;
  position: relative;
  left: -18px;
}

@media (max-width: 360px) {
  .PriceRow .label,
  .PriceRow .value {
    font-size: 15px;
  }
}
