.squareIconLoading,
.squareIcon {
  top: 13px !important;
  right: 0 !important;
  color: white;
  height: 25px !important;
  width: 25px !important;
}

.squareIcon {
  background-color: #05BEC0;
}


.topRow {
  display: inline-block;
  font-weight: bold;
  font-size: 24px;
  line-height: 27px;
  align-items: center;
  text-align: start;
  color: #585858;
}

:global(.Mui-expanded) .topRow {
  margin-top: -11px !important;
}

.topRowIcon {
  margin-right: 23px;
}

.subTitleDetail {
  display: inline-block;
  margin-right: 10px;
}

@media (max-width: 767.9px) {
  .squareIconLoading,
  .squareIcon {
    right: 16px !important;
  }
  .topRowIcon {
    margin-right: 60px;
  }
}
