.topDetails {
  display: block;
  background-color: white;
  margin-bottom: 0px;
  padding-top: 22px;
  padding-right: 31px;

}

.topDetails .detailsTopRow {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-content: flex-start;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
}

.topDetails .detailsTopRow button {
  display: none;
}

.topDetails .title {
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  text-align: right;
  padding-left: 56px;
  color: #323232;
}


.topDetails :global(.LogoSquare) {
  margin: 0 !important;
  float: none !important;
  margin: 0 !important;
  float: none !important;
  position: absolute !important;
  left: 30px;
  top: -3px;
  border: 0.4px #BEBEBE solid;
}

.topDetails .details {
  min-height: 14px;
  margin-top: 26px;
  font-weight: normal !important;
  font-size: 14px !important;
  line-height: 17px !important;
  color: #585858 !important;
}

.topDetails .details :global(.sideIcon) {
  display: none !important;
}

@media (max-width: 767px) {
  .topDetails {
    padding-bottom: 37px;
    min-height: 14px;
    margin-top: 0px;
    font-weight: normal !important;
    font-size: 14px !important;
    line-height: 17px !important;
    color: #585858 !important;
  }
  .topDetails .detailsTopRow {
    padding-left: 25px;
  }

  .topDetails .detailsTopRow .detailsTopRowSide {
    width: fit-content;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: baseline;
    flex-direction: row;
  }
  .topDetails .detailsTopRow button {
    padding: 0;
    margin: 0;
    display: block;
    top: -17px;
  }

  .topDetails .title {
    padding-left: 0;
    width: fit-content;
  }

  .topDetails :global(.LogoSquare) {
    height: 33px;
    position: static !important;
    margin-top: -5px !important;
  }
}
