.checkBoxContainer {
  display: block;
  margin: 0 auto;
  //  padding-right: 105px;
  width: fit-content;
}

.checkBoxContainer :global(.MuiFormControlLabel-root) {
  display: block;
}

.termsContainer {
  min-height: 29px;
}

@media screen and (min-width: 767px) {
  .abstractProduct :global(.stepButtons) {
    height: 181px;
    padding-top: 100px;
  }

  .termsContainer {
    min-height: 29px;
    position: relative !important;
    z-index: 7777777777;
    bottom: -33px !important;
    margin-bottom: -116px !important;
  }
}


@media screen and (max-width: 600px) {
  .checkBoxContainer {
    padding-right: 0px;
  }
}
