.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 8%;
  height: 100%;
  background-color: rgba(35, 33, 33, 0.76);
  z-index: 443;
  border-radius: 3px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.overlay.shown {
  width: fit-content;
  min-width: 50%;
  padding-right: 23px;
}

.overlay.noContent {
  width: 25px;
}

.overlay .grabbableContainer {
  position: absolute;
  cursor: pointer;
  top: 40%;
  left: initial;
  right: -6px;
  width: 26px;
  padding-top: 7px;
}

.overlay.shown .grabbableContainer {
  top: 44%;
  left: initial;
  right: 0px;
  z-index: 999;
}

.grabbable {
  margin-left: auto;
  margin-right: auto;
  width: 4px;
  height: 46px;
  background-color: #FFFFFF;
  border-radius: 3px;
}

.overlay.shown .grabbable {
  height: 26px;
}

.sideNav {
  position: absolute;
  top: 0;
  left: 0;
  width: 8%;
  height: 100%;
  z-index: 444;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.sideNav svg {
  color: white;
  position: relative;
  //top: 40%;
  left: initial;
  right: 22px;
  float: left;
  width: 65px;
  height: 65px;
  cursor: pointer;
}

.leftSideNav {
  z-index: 444;
  overflow: hidden;
  width: 23px;
}

:global(.fullscreen) .leftSideNav {
  background-color: rgba(15, 15, 15, 0.76);
}

.rightSideNav {
  right: 0;
  left: initial;
  width: 25px;
  background-color: rgba(15, 15, 15, 0.76);
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.rightSideNav svg {
  left: -19px;
}

.overlay .overlayContent {
  display: none;
}

.overlay.shown .overlayContent {
  display: block;
  direction: rtl;
  text-align: right;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 29%;
  -webkit-user-select: text !important;
  user-select: text !important;
}
