.locationDate {
  display: flex;
  position: absolute;
  left: 0;
  top: 33px;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  align-content: space-around;
  font-family: Heebo, serif;
  font-weight: normal;

  line-height: 18px;
  color: #323232;
}

:global(.mobile) .locationDate {
  left: initial;
  min-width: 75px;
}


.locationDate .day {
  font-size: 24px;
}

:global(.mobile) .locationDate .day {
  font-size: 18px;
}

.locationDate .month,
.locationDate .year {
  font-size: 12px;
  margin-top: -7px;
}

:global(.mobile) .locationDate .month,
:global(.mobile) .locationDate .year {
  font-size: 13px;
}
