.OuterCard {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: start;
  width: 100%;
  max-width: 722px;
  height: 426px;
  background-color: #05BEC0;
  border-radius: 4px 4px 0px 0px;
  color: white;
  padding-top: 24px;
}

.middle {
  margin-left: auto;
  margin-right: auto;
}


.OuterCard .okIcon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 75px;
  height: 75px;
}

.OuterCard .title {
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 13px;
}

.OuterCard .content {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
  font-weight: normal;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
}


@media (max-width: 596px) {
  .OuterCard {
    padding-left: 10px;
    padding-right: 10px;
  }
  .OuterCard .okIcon {
    width: 30px;
    height: 30px;
  }

  .OuterCard .title {
    font-size: 24px;
    margin-bottom: 5px;
  }

  .OuterCard .content {
    font-size: 15px;
  }
}
