span.loadingField,
button.NextButton:hover,
button.NextButton {
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #F28F17;
  max-width: 189px;
  margin-left: auto;
  margin-right: auto;
  padding: 17px 10px;
  font-size: 20px;
  font-weight: bold;
  border-radius: 50px;
  box-shadow: none;
}

button.NextButton:hover {
  box-shadow: 0 1px 3px rgb(0 0 0 / 12%), 0 1px 2px rgb(0 0 0 / 24%);
}

span.loadingField {
  height: 58px;
}
