.AdStatusContainer .TimeLeft {
  border-radius: 52px;
  border-width: 1px;
  border-style: solid;
  font-weight: bold;

  box-sizing: border-box;
  text-align: center;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
  line-height: 16px;
  padding: 10px;
  min-width: 150px;
  width: fit-content;
  width: -moz-max-content;
  margin-top: 9px;
}

.AdStatusContainer .status {
  display: block;
}

.AdStatusContainer:global(.default) .TimeLeft {
  border-color: #05BEC0;
  color: #05BEC0;
}

.AdStatusContainer:global(.waiting) .TimeLeft {
  border-color: #F28F17;
  color: #F28F17;
}

:global(.approved) .TimeLeft {
  border-color: #05BEC0;
  background-color: #05BEC0;
  color: #FFFFFF;
}

.AdStatusContainer:global(.disapproved) .TimeLeft {
  border-color: #F22417;
  background-color: #F22417;
  color: #FFFFFF;
}


@media screen and (max-width: 1840px) {
  .AdStatusContainer .TimeLeft {
    font-size: 1em;
  }
}

@media screen and (max-width: 1600px) {
  .AdStatusContainer .TimeLeft {
    font-size: 0.7em;
  }
}
