.createProductContainer {
  padding-top: 60px;
}

.createProductContainer .topWhiteTitle {
  position: absolute;
  width: 100%;
  left: 0px;
  right: 12px;
  margin-top: -81px;
  padding-right: 13vw;
  background-color: white;
  padding-top: 10px;
  padding-bottom: 2px;
  display: flex;
  align-items: center;
  text-align: right;
}


.createProductContainer .buttonsContainer {
  margin: 0;
  margin-top: 33px;
  z-index: 99999;
}


.createProductContainer .offerTypeContainer :global(.main) {
  min-height: fit-content !important;
  height: fit-content !important;
  padding-top: 0px;
  padding-bottom: 0px;
}

.editorsRow {
  margin: 0;
}

.top {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 610px) {
  .editorsRow {
    position: relative;
    top: 76px;
    margin-bottom: 76px;
  }
  .createProductContainer .offerTypeContainer :global(.main) {
    margin-bottom: -80px;
  }
}

@media (max-width: 767px) {
  .createProductContainer {
    padding-top: 0;
  }

  .createProductContainer .buttonsContainer {
    margin-bottom: 30px;
  }


}


