.iconContainerClassName {
  position: absolute;
  margin-right: 0;
  top: 10px;
  right: 0;
  width: 32px;
  height: 32px;
  z-index: 99;
  border-radius: 4px;
}

svg.iconClassName {
  background-color: #05BEC0;
  color: white;
  border-radius: 4px;
  padding: 4px;
  width: 32px;
  height: 32px;
}

.iconClassName path {
  color: white;
  fill: white;
}
