.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 10%;
  z-index: 443;
  overflow: hidden;
}

.overlay.shown {
  width: 100%;
  height: 100%;
}

.overlay .grabbableContainer {
  position: absolute;
  cursor: pointer;
  left: 45%;
  bottom: initial;
  top: 10px;
  z-index: 999;

}

.overlay.shown .grabbableContainer {
  bottom: 18px;
  top: initial;
  //top: 83%;
}

.grabbable {
  margin-left: auto;
  margin-right: auto;
  width: 46px;
  height: 4px;
  background-color: #FFFFFF;
  border-radius: 3px;
}

.overlay.shown .grabbable {
  width: 26px;
}

.overlay .overlayContent {

}

.overlay .overlayContent > div {
  top: 0;
  left: 0;
}

.overlay.shown .overlayContent {
  -webkit-user-select: text !important;
  user-select: text !important;
}
