.priceConclusionContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: nowrap;
  margin-top: 10px;
  height: 130px;
  padding-right: 14px;
  padding-left: 14px;
}

.originalLabel,
.originalPrice {
  line-height: 16px;
}

.originalPrice {
  // text-decoration: line-through;
}


.saleLabel {
  font-weight: 600;
  font-size: 18px;
  line-height: 16px;
}

.salePrice {
  font-weight: 600;
  font-size: 24px;
  line-height: 16px;
}

.savings {
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #878787;
  margin-bottom: -10px;
  margin-top: -5px;
}

:global(.card-footer) .priceConclusionContainer .originalLabel,
:global(.card-footer) .priceConclusionContainer .originalPrice,
:global(.card-footer) .priceConclusionContainer .savingsLabel, {
  color: #878787;
}

:global(.card-footer) .priceConclusionContainer .salePrice {
  color: #F28F17;
}

:global(.card-footer) .priceConclusionContainer .saleLabel,
:global(.card-footer) .priceConclusionContainer .savingsPrice, {
  color: black;
}



