
.editButtonContainer {
  padding: 0;
}

.editButton:global(.MuiSkeleton-root.MuiSkeleton-rect) {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  min-height: 40px;
}

.editButton:global(.MuiButton-root),
.editButton:global(.nav-link),
.editButton {
  background-color: #878787 !important;
  border-radius: 52px;
  font-weight: 400;
  font-size: 14px !important;
  line-height: 11.35px;
  text-align: center;
  color: #FFFFFF;
  box-shadow: none;
  width: 160px;
  float: left;
}

.cancelEditor:global(.nav-link),
.cancelEditor:global(.nav-link):hover {
  line-height: 24px;
  display: inline-block;
  min-width: 50px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  margin-bottom: 0;
  color: #878787 !important;
  background-color: #ffffff !important;
  border-style: solid;
  border-width: 1px;
  padding-top: 7px;
  padding-bottom: 7px;
}


.saveEditor:global(.MuiButton-containedPrimary.MuiButton-containedPrimary) {
  line-height: 24px;
  display: inline-block;
  min-width: 50px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  margin-bottom: 0;
  padding: 8px;
  box-shadow: none;
}


@media (max-width: 618.9px) {

  .editButton:global(.MuiButton-root),
  .editButton:global(.nav-link),
  {
    font-size: 14px !important;
  }
}

@media (max-width: 431.9px) {
  .editButton:global(.MuiSkeleton-root.MuiSkeleton-rect) {
    min-height: 28.57px;
  }
  .editButtonContainer {
    padding-left: 5% !important;
    padding-right: 5% !important;
    width: 100%;
  }

  .editButton {
    width: 100%;
  }


  .cancelEditor:global(.nav-link), {
    width: 50%;
    line-height: 0.8;

  }


  .saveEditor:global(.MuiButton-containedPrimary.MuiButton-containedPrimary) {
    width: 50%;
    line-height: 0.8;
  }

  .saveEditor:global(.MuiButton-containedPrimary.MuiButton-containedPrimary) {
    padding: 0.5rem 1.2rem;
  }


}

