.HotelImageSquare {
  color: #05BEC0 !important;
}


.HotelImageSquare .iconContainerClassName {
  background-color: #05BEC0 !important;
}


.HotelImageSquare :global(.image-gallery) {
  position: relative;
  z-index: 1;
  direction: ltr;
}


.HotelImageSquare :global(.image-gallery),
.HotelImageSquare :global(.image-gallery-content),
.HotelImageSquare :global(.image-gallery-slide-wrapper),
.HotelImageSquare :global(.image-gallery-swipe),
.HotelImageSquare :global(.image-gallery-slides),
.HotelImageSquare :global(.image-gallery-slide),
.HotelImageSquare :global(.image-gallery-slide div),
.HotelImageSquare :global(img.image-gallery-image),
{
  height: inherit !important;
  min-height: inherit;
}


.HotelImageSquare :global(.image-gallery-slides) {
  width: 100%;
  position: absolute;
}

.HotelImageSquare :global(.image-gallery),
.HotelImageSquare :global(img.image-gallery-image) {
  object-fit: cover !important;
}

.HotelImageSquare :global(.image-gallery .image-gallery-index) {
  right: 80px;
}

.HotelImageSquare :global(.image-gallery-bullets-container) {
  margin-bottom: -8px;
}

.HotelImageSquare :global(.image-gallery-bullets .image-gallery-bullet) {
  border-radius: 50%;
  padding: 3px !important;
  border: 0;
  background-color: rgba(255, 255, 255, 0.52);
}

.HotelImageSquare :global(.image-gallery-bullets .image-gallery-bullet.active) {
  transform: scale(1.2);
  background-color: #fff;
}


.HotelImageSquare :global(.image-gallery-icon) {
  padding: 0px;
}

.HotelImageSquare :global(.image-gallery-icon .image-gallery-svg) {
  width: initial;
  height: 20px;
  width: 20px;
}

.HotelImageSquare :global(.image-gallery-icon .image-gallery-svg:hover) {
  border-width: 1px;
  border-style: solid;
  border-radius: 25px;
  color: white !important;
}
