.Steps {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 57px;


}


@media (min-width: 767px) {
  .Steps {
    background-color: white;
    filter: drop-shadow(0px 4px 11px rgba(187, 187, 187, 0.21));
    padding-top: 43px;
    padding-left: 64px;
    padding-right: 64px;
    background-color: white;
    height: 100px;
  }
}


@media (max-width: 767px) {
  .Steps {
    padding-top: 24px;
  }
}
