.image_picker {
  margin-top: 32px;
  margin-bottom: 57px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: center;
  align-items: stretch;
  text-align: center;
  align-self: stretch;
  border-radius: 4px;
  z-index: 55;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  -webkit-user-select: none; /* Safari */
  -webkit-touch-callout: none; /* iOS Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -ms-user-select: none; /* Internet Explorer/Edge */
  -moz-user-select: none; /* Old versions of Firefox */
  user-select: none; /* Non-prefixed version*/
}

.skeletonsContainer {
  // width: initial;
  padding-left: 64px;
  padding-right: 64px;
  width: 100%;
  min-width: 100%;
}

.image_picker :global(.checked) {
  display: none;
}

.image_picker :global(.selected .checked) {
  display: block;
  background-color: white;
  width: 20px;
  height: 20px;
  border-radius: 57px;
  position: absolute;
  top: 4px;
  left: 4px;
  color: #BEBEBE;
}

.image_picker :global(.selected .checked) span {
  display: block;
  margin-top: -2px;
  text-align: center;
}

.image_picker {
  border: solid;
  color: transparent;
  border-radius: 8px;
}

.image_picker :global(.responsive) {
  position: relative;
  margin-left: 7px;
  margin-right: 7px;
  border-radius: 3px;
  cursor: pointer;
}

.image_picker :global(.responsive) img,
.image_picker .skeleton {
  border-radius: 3px;
  margin-left: auto;
  margin-right: auto;

  height: 183px !important;
  width: 183px !important;
}

.image_picker .skeleton {
  height: 160px !important;
  width: 160px !important;
}


.valid,
.error {
  position: initial;
  // position: absolute;
}


@media (max-width: 812px) {

  .image_picker {
    margin-top: 14px;
    margin-bottom: 23px;
  }

  .image_picker :global(.responsive) img,
  .image_picker .skeleton {
    height: 75px !important;
    width: 75px !important;
  }

}
