.TextField {
  height: 56px;
  border-radius: 4px;
  border: 0.4px solid #BEBEBE;
  box-sizing: border-box;
  cursor: text;
  align-items: center;
  margin: 0;
  background-color: white;

  font-size: 18px;
  line-height: 16px;
  text-align: start;
  color: #BEBEBE;
  background-image: url("/assets/img/dateIcon.svg");
  background-repeat: no-repeat;
  background-position: 25px 50%;

}

.TextField :global(.MuiFormLabel-filled) {
  top: 12px;
}

.TextField label {
  font-size: 18px;
  line-height: 16px;
  color: #585858 !important;
}

.TextField label:global(.MuiInputLabel-shrink) {
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  text-align: start;
  color: #BEBEBE !important;
  top: 12px;
}


.TextField fieldset legend {
  width: 0;
}

.TextField.readOnly input {
  cursor: default;
  color: #585858;
}

.TextField input {
  padding-top: 26px;
  padding-bottom: 12px;
}

.TextField.readOnly :global(.Mui-focused) {
  color: #BEBEBE;
  border-color: #BEBEBE;
}

.TextField :global(.Mui-focused) fieldset {
  border-color: #BEBEBE !important;
}

.TextField.readOnly :global(.MuiInputBase-formControl:hover),
.TextField.readOnly :global(.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline) {
  border: 0.4px solid #BEBEBE !important;
}

.TextField.readOnly :global(.MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline) {
  border-color: #BEBEBE !important;
  border-width: 0;
}

.TextField.noBorder,
.TextField.noBorder fieldset {
  border-color: white;
}

.TextField.error fieldset {
  border-width: 1px;
  color: #c13515 !important;
  box-shadow: #c13515 0px 0px 0px 1px inset !important;
  background-color: #fff8f6 !important;
  border-color: #c13515 !important;
}

.valid fieldset {
  color: #BEBEBE;
  //border-width: 2px;
  //border-color: #00fda2;
}


