.locationTime {
  position: absolute;
  top: 42px;
  right: 95px;
  width: 100px;
  height: 46px;
  font-family: Heebo, serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 18px;
  color: #000000;

}

:global(.mobile) .locationTime {
  right: 16%;
  top: 35px;
}

.locationTime h5.time {
  margin-left: auto;
  margin-right: auto;
  font-family: Heebo, serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 18px;
  width: fit-content;
  text-align: center;
}

:global(.mobile) .locationTime h5.time {
  font-size: 18px;
}

.locationTime .city,
.locationTime .iata {
  margin-left: auto;
  margin-right: auto;
  font-weight: normal;
  font-size: 12px;
  line-height: 18px;
  color: #323232;
  width: fit-content;
  text-align: center;
}

:global(.mobile) .locationTime .city,
:global(.mobile) .locationTime .iata {
  font-size: 13px;
}

.locationTime.flip {
  margin-right: 57%;
}

:global(.mobile) .locationTime.flip {

}
