.SaleDetailsForm .hotelInfo {

  background-color: white;
  padding: 40px;
  margin-bottom: 25px;
}

.SaleDetailsForm .hotelInfo :global(.title) {
  color: #323232;
  font-weight: bold;
  font-size: 32px;
  line-height: 24px;
}


@media (max-width: 599.9px) {
  .SaleDetailsForm .hotelInfo :global(.title) {
    color: #323232;
    font-size: 17px;
    line-height: 32px;
  }


}
