.infoCardContainer {
  padding: 0;
  margin-bottom: 33px;
  width: 100%;
  padding-right: 41px;
  padding-left: 41px;

}

.infoCardContainer:global(.Mui-expanded) {
  padding-top: 20px;
  padding-bottom: 20px;
}

.infoCardContainer :global(.MuiAccordionDetails-root) {
  padding: 0;
}

.infoCardContainer :global(.MuiAccordionSummary-expandIcon) {
  display: none;
}


.infoCardContainer :global(.MuiAccordionDetails-root) {
  display: flex;
  flex-direction: column;
}

.infoCardContainer :global(.MuiAccordionSummary-content.Mui-expanded) {
  margin: 0;
  margin-top: 12px;
}

.infoCardContainer:global(.MuiAccordion-root:before) {
  opacity: 0;
}

.infoCardContainer :global(.MuiButtonBase-root.MuiAccordionSummary-root.Mui-expanded) {
  min-height: 57px;
}

@media (max-width: 767px) {
  .infoCardContainer :global(.MuiAccordionSummary-expandIcon) {
    display: initial;
  }
  .infoCardContainer:global(.Mui-expanded) :global(.MuiAccordionSummary-expandIcon) {
    // margin-left: -24px;
  }

  .infoCardContainer,
  .infoCardContainer :global(.MuiAccordionSummary-root) {
    padding: 0;
  }
  .infoCardContainer:global(.Mui-expanded) {
    padding: 0;
    // padding: 15px;
  }

  .infoCardContainer :global(.MuiCollapse-container) {
    padding-right: 16px;
    padding-left: 16px;
  }

  .infoCardContainer:global(.Mui-expanded) :global(.MuiCollapse-container) {
    padding-bottom: 33px;
  }


}
