.Radio {
  margin-top: -4px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-evenly;
  cursor: pointer;
  width: 67%;
  margin-right: -16px;

}

.Radio .between {
  line-height: 28px;
}

.Radio .RadioCircleWithLabel {
  display: inline-block;
}

.Radio .RadioCircleWithLabel .label {
  margin-right: 5px;
}

.Radio svg {
  cursor: pointer;
  width: 26px;
  height: 26px;
}

@media (max-width: 767px) {
  .Radio .value {
    //  margin-left: 10px;
    //  margin-right: 10px;
  }
}

@media (max-width: 300px) {
  .Radio svg {
    width: 24px;
    height: 24px;
  }

}
