.FormikDescription {
  background-color: white;
  height: 187px;
}

.FormikDescription :global(.MuiFormHelperText-root) {
  margin-top: 10px;
}

.FormikDescription textarea {
  color: #585858;
}

.FormikDescription label[data-shrink="true"] {
  z-index: 100;
  top: 10px;
  right: 8px;
  font-size: 13px;
  line-height: 20px;
  color: #BEBEBE !important;
}


.FormikDescription fieldset legend {
  width: 0;
}

.charactersAmount,
.additionalInfo {
  position: absolute;
  font-weight: 300;
  font-size: 13px;
  line-height: 20px;
  text-align: start;
  color: #BABFC3;
}

.additionalInfo {
  margin-top: 13px;
}


.FormikDescription :global(.Mui-focused) fieldset {
  border-color: #BEBEBE !important;
}

@media (max-width: 409px) {
  .FormikDescription textarea {
    padding-top: 15px;
  }
}
