.Slider {

}

.Slider:global(.MuiSlider-root) {
  color: #05BEC0;
  height: 8px;
}

.Slider:global(.MuiSlider-root) :global(.MuiSlider-thumb) {
  width: 25px;
  height: 25px;
  background-color: #FFFFFF;
  border: 1px solid #05BEC0;
  box-sizing: border-box;
  margin-top: -8px;
  margin-left: -12px;
}


.Slider:global(.MuiSlider-root) > :global(span.MuiSlider-rail),
.Slider:global(.MuiSlider-root) > :global(span.MuiSlider-track) {
  height: 9.33px !important;

}

.Slider:global(.MuiSlider-root) > :global(span.MuiSlider-rail) {
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
}

.Slider:global(.MuiSlider-root) > :global(span.MuiSlider-track) {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
}

.Slider :global(.MuiSlider-thumb .MuiSlider-valueLabel) {
  right: initial;
}

.Slider :global(.MuiSlider-thumb .MuiSlider-valueLabel) > span > span {
  color: white;
}
