.guaranteePoints {
  display: block;
  background-color: #E3F1F1;
}

.guaranteePoints .verticalDivider {
  width: 1px;
  background-image: linear-gradient(to bottom, #878787 40%, rgba(255, 255, 255, 0) 0%);
  background-position: left;
  background-size: 0.3px 9px;
  background-repeat: repeat-y;
}

.guaranteePoints .horizontalDivider {
  height: 2px;
  background-image: linear-gradient(to right, #878787 40%, rgba(255, 255, 255, 0) 0%);
  background-position: top;
  background-size: 8px 3.4%;
  background-repeat: repeat-x;
  margin-top: 16px;
  margin-bottom: -16px;
}

@media (min-width: 767px) {
  .guaranteePoints .verticalDivider,
  .guaranteePoints .horizontalDivider {
    display: none;
  }
}
