.purchaseButton:global(.btn.btn-block) {
  margin-top: 25px !important;
  margin-bottom: 25px !important;
}

.bottomMargin {
  margin-top: 135px;
}

@media (max-width: 767px) {

  .bottomMargin {
    display: none;
  }

  .mobileChatIcon {
    cursor: pointer;
    position: absolute;
    margin-right: -286px;
  }
  .guaranteePoints {
    display: flex !important;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
  }
  .guaranteePoints > div:first-of-type {
    display: flex !important;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
  }
  .guaranteePoints,
  .guaranteePoints > div > div {
    background-color: transparent !important;
  }

  .guaranteePoints > div > div {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    padding: 0;
  }
  .guaranteePoints > div > div > span {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 64px;
    text-align: center;
  }

  .mobilePurchaseButton {
    background: #E3F1F1;
    height: 74px;
    display: flex;
    padding: 0px;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: flex-start;
    align-items: center;
    margin-top: -13px;

    overflow-y: hidden;
    max-height: 74px; /* approximate max height */

    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;

  }
  .mobilePurchaseButton button {
    width: 192px;
    margin: 0px !important;
    max-height: 49px !important;
    font-size: 15px !important;
    line-height: 24px !important;
  }

  .mobilePurchaseButton.mobileSticky {
    position: fixed;
    width: 100%;
    position: fixed;
    bottom: 0px;
    width: 100%;
    z-index: 99999999;
    transition: all .3s ease-out;
  }
  .mobilePurchaseButton.mobileStickyPlaceHolderHide {
    display: none;
  }
  .mobilePurchaseButton.mobileStickyPlaceHolderShow {
    display: block;
  }

}
