.howItWorks {
  background-color: #585858 !important;
  color: #FFFFFF !important;
  cursor: pointer;
  margin-bottom: -3px;
}

.howItWorks span {
  color: #FFFFFF !important;
}

.points {
  margin-bottom: -13px;
  background-color: white;
  padding-top: 19px;
  margin-top: -6px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.point {
  min-height: 32px !important;
  padding-right: 27px;
  margin-bottom: 0px;
  margin-top: -8px;
  background-color: transparent !important;
}

.point span {
  margin-left: 16px;
}


.pointSupportLink {
  height: 54px;
}

.pointSupportLink span {
  cursor: pointer;
  font-weight: 600;
  font-size: 12px;
  line-height: 36px;
  color: #05BEC0 !important;
}

@media (min-width: 767px) {
  .howItWorks {
    cursor: initial;
  }
  :global(.MuiCollapse-hidden).points {
    height: auto !important;
    visibility: initial;
  }
  :global(.MuiCollapse-hidden).points :global(.MuiCollapse-wrapperInner) {
    visibility: initial;
  }
}

@media (max-width: 767px) {
  .points:global(.MuiCollapse-entered) {
    margin-bottom: 10px !important;
  }
}
