.actionButtons:global(.MuiButtonBase-root.MuiButton-root) {
  display: inline-block;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #05BEC0;
  float: left;
  margin-bottom: 0;
  box-shadow: none !important;
}


.approveButton:global(.MuiButtonBase-root.MuiButton-root) {
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  border-style: solid;
  border-width: 1px;
  padding-top: 8px;
  padding-bottom: 6px;
  margin-top: 0px;
  margin-right: -1px;
  border-color: #05BEC0;
}

.approveButton:global(.MuiButtonBase-root.MuiButton-root):hover {
  color: #05BEC0;
  background-color: #ffffff !important;
}


@media (max-width: 260.9px) {
  .actionButtons:global(.MuiButtonBase-root.MuiButton-root) {
    font-size: 16px !important;
    min-width: 60px;
  }
}
