.adHotelReview {
  padding: 2vw;
}

.adHotelReview .time {
  text-align: end;
  color: #C1C1C1;
  font-size: 14px;
  margin-top: 10px;
}

.adHotelReview .name {
  padding: 0 !important;
  margin: 0 !important;
  color: #878787;
  font-weight: 500;
  font-size: 14px;
}

.adHotelReview .name .profilePhoto {
  width: 40px;
  height: 40px;
  border-radius: 100px;
  margin-left: 10px;
}

.adHotelReview .rating {
  float: left;
}

.adHotelReview .rating :global(.MuiRating-icon svg) {
  width: 16px;
}
