.Radio {
  color: #BEBEBE;
  float: left;
  margin-left: 17px;
  margin-top: -4px;
  width: 26%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  max-width: 64px;
}

.labelInMiddleMode {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 95%;
  padding-left: 21px;
  padding-right: 8px;
}

.Radio svg {
  cursor: pointer;
  width: 26px;
  height: 26px;

}

.Radio .value {
  min-width: 56px;
  display: inline-block;
  text-align: center;
  color: #323232;
}

@media (max-width: 767px) {
  .Radio .value {
    //  margin-left: 10px;
    //  margin-right: 10px;
  }
}

@media (max-width: 300px) {
  .Radio .value {
    min-width: 4vw;
    font-size: 14px;
    //  margin-left: 2px;
    //  margin-right: 2px;
  }
  .Radio svg {
    width: 24px;
    height: 24px;
  }

}
