.AuthenticationCard {
  background-color: #FFFFFF;
  max-width: 100% !important;
  width: 100% !important;
  box-shadow: none;
  padding: 0;
  margin: 0;
}

.AuthenticationCard h1 {
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  text-align: center;
}


.AuthenticationCard :global(button.facebookButton) {
  background-color: #3B5998;
  background-repeat: no-repeat;
  background-position: 20% 13px;
}


.AuthenticationCard :global(button.googleButton) {
  color: rgba(0, 0, 0, 0.54);
  background-color: #FFFFFF;
  background-repeat: no-repeat;
  background-position: 20% 13px;
}



