.adCard {
  margin-bottom: 53px;
}

.adCard :global(.card) {
  box-shadow: none !important;
  border-radius: 3px !important;
}

.adCard :global(.card) > :global(.card-body) {
  padding: 0;
}

.adCard > .adCardTitle {
  font-weight: 700 !important;
  text-align: start;
  color: #323232;
  font-size: 18px;
}

.adCard > .adCardTitle > .detailLine {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  text-align: right;
  color: #585858;
}

.adCard > .detailLine > .title {
  color: #878787;
}

@media (max-width: 992px) {
  .adCard {
    margin-bottom: 20px;
  }
}


@media (max-width: 767px) {


  .adCard :global(.card),
  .adCard :global(.card) > :global(.card-body) {
    box-shadow: none;
    border-radius: 12px;
  }

}
