.AbstractDetailsForm :global(.MuiFormLabel-root.MuiInputLabel-formControl) {
  width: 91%;
}

.AbstractDetailsForm {
  overflow: hidden;
}


.AbstractDetailsForm label:global(.MuiFormControlLabel-root) {
  margin: 0 !important;
}

.AbstractDetailsForm :global(.MuiFormLabel-root.MuiInputLabel-formControl.MuiInputLabel-shrink) {
  width: fit-content;
}

.paymentOptions,
.guaranteePoints {
  display: none !important;
}

@media (min-width: 767px) {
  .form {
    padding-bottom: 26px;
  }
}

@media (max-width: 767px) {
  .form {
    padding-bottom: 8px;
  }
  .buttonsContainer {
    padding: 0 !important;
    padding-top: 8px !important;
  }
  .paymentOptions {
    display: flex !important;
  }
  .mobileChatIcon {
    cursor: pointer;
    position: absolute;
    margin-right: -286px;
  }
  .guaranteePoints {

    display: flex !important;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
  }
  .guaranteePoints > div:first-of-type {
    display: flex !important;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
  }
  .buttonsContainer,
  .guaranteePoints,
  .guaranteePoints > div > div {
    background-color: white !important;
  }

  .guaranteePoints > div > div {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    padding: 0;
  }
  .guaranteePoints > div > div > span {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 64px;
    text-align: center;
  }
}
