.LocationFlightCompany {
  position: absolute;
  left: 0;
  height: 46px;
  color: #585858;
}


:global(.mobile) .LocationFlightCompany {
  left: initial;
  right: 46%;
  top: 47px;
  color: #F28F17;
  font-size: 12px;
  padding-top: 6px;
}


.LocationFlightCompany :global(.LogoSquare ) {
  background-color: transparent !important;
  height: fit-content !important;
  padding: 0 !important;
  margin: 0 !important;
  max-width: 20px;
}

.LocationFlightCompany .airlineName {
  font-family: Heebo, serif;
  font-weight: normal;
  font-size: 10px;
  margin-left: 7px;
}

