.topTitle {
  color: #585858;
  font-weight: 500;
  font-size: 18px;
  line-height: 16px;
}

.hotelDetails :global(.MuiGrid-grid-sm-auto) {
  display: none;
}

@media (max-width: 600px) {
  .hotelDetails :global(.MuiGrid-grid-sm-true) {
    width: 50%;
  }
}

@media (max-width: 340px) {
  .hotelDetails :global(.MuiGrid-grid-sm-true) {
    width: 100%;
  }
}
