.statusSelector {
  text-align: right !important;
  margin-top: -10px !important;
  max-width: 196px !important;
  margin-left: auto !important;
  margin-right: auto !important;
  display: block !important;
  padding-top: 8px !important;
}

.statusSelector label {
  font-size: 15px;
  width: 100% !important;
  text-align: center;
}

.statusSelector :global(.MuiFormLabel-root.MuiInputLabel-root.MuiInputLabel-formControl) {
  background-color: transparent !important;
  z-index: 401 !important;
  margin-bottom: -8px !important;
  color: rgba(0, 0, 0, 0.54);
  width: 100%;
  text-align: center !important;
  margin-right: -14px !important;
  position: absolute !important;
  top: -11px !important;
}


.statusSelector :global(.MuiFormLabel-root.MuiInputLabel-root.MuiInputLabel-formControl.MuiInputLabel-shrink) {
  // margin-bottom: -23px !important;
  // margin-right: -43px !important;
  // top: 8px !important;
  // position: absolute !important;
  display: none !important;
}


.statusSelector :global(.SearchSelector__single-value) {
  width: 100% !important;
}

.statusSelector :global(.MuiGrid-container p.MuiTypography-root) {
  line-height: initial !important;
  font-size: 13px;
}


.statusSelector :global(.SearchSelector__control) {
  padding: 0 !important;
  padding-right: 14% !important;
  z-index: 400 !important;
  background-color: #FFFFFF !important;
  border: 1px solid #05BEC0 !important;
  border-radius: 48px !important;
  top: 0 !important;
  width: inherit !important;
  min-height: 26px;
  height: 26px !important;
  padding-left: 21px !important;
  font-size: 14px !important;
  line-height: 17px !important;
  color: #323232 !important;
}

.statusSelector :global(.SearchSelector__menu-list) {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}

.statusSelector :global(.SearchSelector__menu-list):first-child {
  border-top-right-radius: 25px !important;
  border-top-left-radius: 25px !important;
}


.statusSelector :global(.SearchSelector__menu-list):last-child {
  border-bottom-right-radius: 25px !important;
  border-bottom-left-radius: 25px !important;
}


.statusSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar {
  width: 3px !important; /* width of the entire scrollbar */
}

.statusSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track {
}

.statusSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-thumb {
  background-color: #05BEC0 !important; /* color of the scroll thumb */
  border-radius: 20px !important; /* roundness of the scroll thumb */
}

.statusSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track-piece:end {
  background-color: transparent !important;
  margin-bottom: 15px !important;
}

.statusSelector :global(.SearchSelector__menu-list)::-webkit-scrollbar-track-piece:start {
  background-color: transparent !important;
  margin-top: 15px !important;
}


.statusSelector :global(.SearchSelector__control .SearchSelector__placeholder) {
  color: #05BEC0 !important;
  font-weight: 700 !important;
  font-size: 14px !important;
  line-height: 24px !important;
  text-align: right !important;
  margin-right: auto !important;
  margin-left: auto !important;
}

.statusSelector :global(.SearchSelector__control ):hover {
  background-color: white !important;
}

.statusSelector :global(.SearchSelector__control.SearchSelector__control--menu-is-open) {
  border-radius: 25px !important;
}

.statusSelector :global(.SearchSelectorContainer .MuiFormControl-root .SearchSelector__menu) {
  z-index: 402 !important;
  border-width: 1px !important;
  border-style: solid !important;
  background-color: rgb(218, 248, 249) !important;
  border-radius: 52px !important;
  position: absolute !important;
}


.statusSelector :global(div.SearchSelector__menu) {
  border-radius: 25px !important;
  z-index: 402 !important;
  background-color: rgb(218, 248, 249) !important;

  box-shadow: none !important;
  border: 1px solid transparent !important;

  margin-top: 7px !important;

  // padding-left: 20px;
  // padding-right: 20px;

}


.statusSelector :global(div.SearchSelector__menu .SearchSelector__menu-list) {
}

.statusSelector :global(.valid.SearchSelector__control) {

}

.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option) {
  background-color: transparent !important;
  font-weight: 500 !important;
  font-size: 14px !important;
  line-height: 24px !important;
  z-index: 402 !important;
  text-align: right !important;
  color: #323232 !important;
  padding: 0 !important;
}

.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option) > div:global(.MuiGrid-root) {
  padding: 8px 16px;

}

.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option) .optionText {
  color: #323232 !important;
}

.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option) .optionText {
  color: #323232 !important;
}

.statusSelector .optionCount {
  float: left !important;
  margin-left: 2px !important;
}


.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option:hover) {
  background-color: #FBFBFB !important;
}

.statusSelector :global(div.SearchSelector__menu div.SearchSelector__option.last:hover) {
  border-bottom-left-radius: 20px !important;
  border-bottom-right-radius: 20px !important;
}


.statusSelector :global(.SearchSelector__value-container--has-value) {
  padding-top: 0;
}

.statusSelector :global(div.SearchSelector__value-container.SearchSelector__value-container--has-value div.SearchSelector__single-value) {
  font-size: 25px !important;
  color: #323232 !important;
  padding-left: 15px !important;
}

.statusSelector :global(.SearchSelector__option--is-selected) {
  display: none !important;
}

.statusSelector :global(.SearchSelector__indicator-separator) {
  display: none !important;
}

.statusSelector .searchIcon {
  -webkit-transform: scaleX(-1) !important;
  transform: scaleX(-1) !important;
  margin-right: 14px !important;
  color: #BEBEBE !important;
  fill: #BEBEBE !important;
}


@media (max-width: 549px) {
  .statusSelector {
    max-width: 100% !important;
    //margin: 0 !important;
  }

}

