.InnerCard {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: start;
  width: 92%;
  max-width: 648px;
  // height: 270px;
  background-color: #FFFFFF;
  box-shadow: 2px 2px 10px rgb(50 30 5 / 22%);
  border-radius: 20px;
  /* position: absolute; */
  position: relative;
  /* right: 12.7%; */
  bottom: -16px;
  color: #585858;
  padding-top: 28px;
  padding-bottom: 21px;
}

.middle {
  margin-left: auto;
  margin-right: auto;
}

ol.middle {
  max-width: 300px;
  padding: 0;
}


.InnerCard .title {
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  margin-bottom: 18px;
}

.InnerCard .content {
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;
  text-align: start;
  padding-right: 139px;
  padding-left: 40px;
}


@media (max-width: 596px) {
  .InnerCard {
    padding-top: 20px;
    padding-bottom: 21px;
  }
  .InnerCard .title {
    font-size: 17px;
    margin-bottom: 12px;
    line-height: initial;
  }
  .InnerCard .content {
    font-size: 15px;
    padding: 0;
    padding-left: 12px;
    padding-right: 40px;
  }
}
