.HotelImagesBigSquare {
  direction: ltr;
  width: 100%;
  max-height: 100%;

}

.HotelImagesBigSquare :global(.image-gallery),
.HotelImagesBigSquare :global(.image-gallery-content),
.HotelImagesBigSquare :global(.image-gallery-slide-wrapper),
.HotelImagesBigSquare :global(.image-gallery-swipe),
.HotelImagesBigSquare :global(.image-gallery-slides),
.HotelImagesBigSquare :global(.image-gallery-slide),
.HotelImagesBigSquare :global(.image-gallery-slide div),
.HotelImagesBigSquare :global(img.image-gallery-image),
{
  height: inherit !important;
  min-height: inherit;
}

.HotelImagesBigSquare :global(.image-gallery-thumbnails-wrapper.right) {
  margin-right: 0;
}

.HotelImagesBigSquare :global(.image-gallery-thumbnails-container) > button:first-child {
  margin-top: -4px;
}

.HotelImagesBigSquare :global(.image-gallery-content .image-gallery-slide div) {
  // -webkit-box-shadow: 0 5px 5px 1px #f2f2f2;
  // box-shadow: 0 5px 5px 1px #f2f2f2;
  border-radius: 3px;
}

.HotelImagesBigSquare :global(button.image-gallery-thumbnail) {
  width: fit-content;
}

.HotelImagesBigSquare :global(.image-gallery-thumbnail-inner) {
  // -webkit-box-shadow: 0px 0px 5px #888, 0px -2px 5px #888;
  // box-shadow: 0px 0px 5px #888, 0px -2px 5px #888;
  border-radius: 3px;
}

.HotelImagesBigSquare :global(.image-gallery-thumbnail.active),
.HotelImagesBigSquare :global(.image-gallery-thumbnail:hover) {
  // border-color: #05bec052;
  border-color: transparent;
  // border-radius: 3px;
  // -webkit-box-shadow: 0px 0px 5px #888, 0px -2px 5px #888;
  // box-shadow: 0px 0px 5px #888, 0px -2px 5px #888;
}

.HotelImagesBigSquare :global(.image-gallery-thumbnail.active .image-gallery-thumbnail-inner),
.HotelImagesBigSquare :global(.image-gallery-thumbnail:hover .image-gallery-thumbnail-inner) {
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 3px;
}


.HotelImagesBigSquare :global(img.image-gallery-image),
.HotelImagesBigSquare :global(.image-gallery-content .image-gallery-slide .image-gallery-image) {
  width: 100%;
  height: 371px;
  max-height: 371px;
  object-fit: cover;
  -webkit-box-shadow: inherit;
  box-shadow: inherit;
  border-radius: inherit;
}

.HotelImagesBigSquare :global(.image-gallery-content) {
  border-radius: 3px;
}

.HotelImagesBigSquare :global(img.image-gallery-thumbnail-image), {
  width: 89px;
  height: 88px;
  max-height: 85px;
  object-fit: cover;
  -webkit-box-shadow: inherit;
  box-shadow: inherit;
  border-radius: inherit;
}

.HotelImagesBigSquare:global(.image-gallery .image-gallery-index) {
  right: 1px;
  font-size: 14px;
  padding: 2px;
  border-radius: 25px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 0;
}

.HotelImagesBigSquare:global(.image-gallery .image-gallery-index .image-gallery-index-current) {
  margin-left: 7px;
}

.HotelImagesBigSquare :global(.image-gallery-icon) {
  z-index: 445;
}


.mobile {

}

.HotelImagesBigSquare.mobile :global(.image-gallery-thumbnails-container) > button:first-child {
  margin-top: initial;
}

.mobile :global(.image-gallery-thumbnails-container) {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.mobile :global(.image-gallery-slides) > div:global(.overlay) {
  //width: 6%;
}

.mobile :global(.image-gallery-slides) > div:global(.overlay .grabbableContainer) {
  // display: none;
}


.mobile :global(.image-gallery-thumbnails-container) > button,
.mobile :global(.image-gallery-thumbnails-container) > button > span,
.mobile :global(.image-gallery-thumbnails-container) > button > span > img {
  min-height: 75px;
  min-width: 75px;
  height: 75px;
  width: 75px;
  max-height: 75px;
  max-width: 75px;

  width: 100%;
  height: 100%;
}

@media (max-width: 575.9px) {
  .HotelImagesBigSquare.mobile :global(img.image-gallery-image),
  .HotelImagesBigSquare.mobile :global(.image-gallery-content .image-gallery-slide .image-gallery-image) {
    min-height: 149px;
    height: 149px;
    max-height: 149px;
  }
}


.HotelImagesBigSquare :global(.fullscreen .overlay) {
  display: none !important;
}

.HotelImagesBigSquare :global(.fullscreen img.image-gallery-image),
.HotelImagesBigSquare :global(.fullscreen .image-gallery-content .image-gallery-slide .image-gallery-image) {
  width: 100% !important;
  height: 100% !important;
  max-height: 100% !important;
  object-fit: contain;
  -webkit-box-shadow: inherit;
  box-shadow: inherit;
  border-radius: inherit;
}

.HotelImagesBigSquare :global(.image-gallery-icon.image-gallery-fullscreen-button) {
  right: 10px;
  position: absolute;
}
