.stepButtons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-top: 32px;
  position: relative;
}

.stepButtons .spacing {
  width: 30px;
}

.stepButtons .chatButton {
  margin: 0px;
  right: 5%;
  margin-top: 10px;
}

@media (min-width: 767px) {
  .stepButtons {
    background: #FFFFFF;
    box-shadow: 0px -4px 11px rgba(187, 187, 187, 0.21);
    height: 88px;
    padding-top: 20px;
  }
}

@media (max-width: 767px) {
  .stepButtons {
    background: #E3F1F1;
    box-shadow: 0px -4px 11px rgba(187, 187, 187, 0.21);
    padding-top: 15px;
    padding-bottom: 15px;
    flex-direction: column-reverse;
    flex-wrap: nowrap;
  }

  .stepButtons .chatButton {
    top: 20%;
  }

}

@media (max-width: 340px) {

  .stepButtons {
    height: 118px;
    flex-direction: column-reverse;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

}
