.rightSideLoader {
  background-color: #05BEC0 !important;
  width: 100% !important;
  height: 351px !important;
}

.secondRow {
  margin-top: 16px;
}

@media (max-width: 767.9px) {
  .topRow {
    flex: unset;
    max-width: 100%;
  }

  .secondRow {
    margin-top: -16px;
  }

  .navigateBackRow {
    position: absolute;
    width: 100%;
    top: 18px;
  }

  .navigateBackRow :global(.nav-link) {
    display: none;
  }
}
