.PlaceImagePicker {
  background-color: white;
  padding-top: 22px;
  padding-bottom: 1px;
  -webkit-user-select: unset; /* Safari */
  -webkit-touch-callout: unset; /* iOS Safari */
  -khtml-user-select: unset; /* Konqueror HTML */
  -ms-user-select: unset; /* Internet Explorer/Edge */
  -moz-user-select: unset; /* Old versions of Firefox */
  user-select: unset; /* Non-prefixed version*/
}

.PlaceImagePicker.error {
  border-width: 1px;
  color: #c13515 !important;
  box-shadow: #c13515 0px 0px 0px 1px inset !important;
  background-color: #fff8f6 !important;
  border-color: #c13515 !important;
}


.PlaceImagePicker .valid {
  // border-color: #00fda2;
  border-color: transparent;
}

.PlaceImagePicker p.error {
  color: #c13515 !important;
  border-color: transparent;
  position: absolute;
  margin-right: 62px;
}

.PlaceImagePicker .topTitle {
  font-weight: normal;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  color: #BEBEBE;
  -webkit-user-select: all; /* Safari */
  -webkit-touch-callout: default; /* iOS Safari */
  -khtml-user-select: all; /* Konqueror HTML */
  -ms-user-select: all; /* Internet Explorer/Edge */
  -moz-user-select: all; /* Old versions of Firefox */
  user-select: all; /* Non-prefixed version*/
}


.imagesElected {
  position: absolute;
  font-weight: 300;
  font-size: 13px;
  line-height: 20px;
  text-align: start;
  color: #BABFC3;
}


.imagesElected span:global(.MuiSkeleton-text) {
  width: 100px;
  height: 14px;
  margin-top: 5px;
}
