.AdControlTopLine.topWhiteTitle {
  margin-top: -34px;
  background-color: #323232;
  padding-top: 15px;
  padding-bottom: 10px;
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  align-items: center;
  text-align: right;
  color: #585858;
  margin-bottom: 45px;
  z-index: 501 !important;

  position: sticky;
  top: 58px;
  width: 100%;
  display: block;
  overflow: hidden;
}


.AdControlTopLine .AdStatus .TimeLeft {
  margin: initial;
  font-size: 24px;
  line-height: 20px;
}

.AdControlTopLine.top-nav-collapse {
  margin-top: -18px;
  position: fixed;
  top: 82px;
  right: 0;
  left: 0;
  z-index: 1030;
}


.AdControlTopLine.top-nav-collapse .statusColumn {
  padding-top: 5px;
}

@media (max-width: 1199.9px) {
  .AdControlTopLine :global(.container) {
    padding: 0;
    margin: 0;
    max-width: none;
  }
}

@media (max-width: 767.9px) {
  .AdControlTopLine.topWhiteTitle {
    top: 50px;
    margin-top: -50px;
  }
  .AdControlTopLine .statusColumn {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: fit-content;
    flex: none;
  }

}

.AdControlTopLine .statusColumn :global(.AdStatusContainer) {
  font-weight: 500;
  font-size: 24px;
  color: #ffffff;
}

@media (max-width: 614.9px) {
  .AdControlTopLine .statusColumn :global(.AdStatusContainer.approved) {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    margin-bottom: 14px;
  }
}


@media (min-width: 498px) {
  .AdControlTopLine .row {
    width: 100%;
  }
}


@media (max-width: 431.9px) {

  .AdControlTopLine .statusColumn,
    // .AdControlTopLine .statusColumn :global(a.nav-link),
    // .AdControlTopLine .statusColumn :global(a.nav-link button)
  {
    width: 100%;
  }

  .AdControlTopLine .statusColumn :global(a.nav-link) {
    // padding-left: 5% !important;
    // padding-right: 5% !important;
  }
  .AdControlTopLine .statusColumn :global(.AdStatusContainer) {
    margin-bottom: 14px;
  }


  .cancelEditor:global(.nav-link), {
    border-bottom-left-radius: 52px;
    float: right;
    width: 100px;
  }

  .saveEditor:global(.MuiButton-containedPrimary.MuiButton-containedPrimary) {
    border-top-right-radius: 52px;
    float: left;
    width: 100px;
  }

}
