.CustomizedTextField {
  display: block;
  width: 100%;
  height: 56px;
  background-color: white;
  font-size: 18px;
  line-height: 16px;
  text-align: start;
  color: #BEBEBE;
  border-radius: 4px;
  padding-top: 19px;
  padding-right: 17px;
  margin-top: 4px;
}


.CustomizedTextField .SearchSelectErrorsContainer {
  position: absolute;
  margin-top: 18px;
}

.CustomizedTextField .SearchSelectError {
  color: rgb(193, 53, 21) !important;
}


.CustomizedTextField.fieldError {
  border-width: 1px;
  color: #c13515 !important;
  box-shadow: #c13515 0px 0px 0px 1px inset !important;
  background-color: #fff8f6 !important;
  border-color: #c13515 !important;
}
