.flightInfoLine {
  height: 104px;
  position: relative;
  // margin-bottom: 31px;
}


.flightInfoLine :global(.showtheway ) {
  float: unset;
  position: absolute;
  top: 137px;
  right: 26px;
}

.flightInfoLine :global(.showtheway ) img {
  max-height: 16px;
  border-radius: 9px;
}


.flightInfoLine h4 {
  position: absolute;
  top: 0;
}

.flightInfoLine .airlineLogo {
  position: absolute;
  top: 34px;
  z-index: 500;
  width: 48px;
  height: 48px;
}


.flightInfoLine .flightDirectionTitle {
  font-size: 18px;
  font-weight: bold;
  position: absolute;
  top: 50px;
}

:global(.mobile) .flightInfoLine .flightDirectionTitle {
  display: none;
}

.timeDateDivider {
  position: absolute;
  height: 47%;
  left: 65px;
  top: 37px;
  width: 1px;
  background-image: linear-gradient(to bottom, #878787 40%, rgba(255, 255, 255, 0) 0%);
  background-position: left;
  background-size: 0.3px 9px;
  background-repeat: repeat-y;
}

:global(.mobile) .timeDateDivider {
  left: initial;
  right: 64px;
  height: 68px;
}

:global(.mobile .secondLine) .timeDateDivider {
  margin-top: -34px;
  height: 97px;
}
