.GenericDetail {
  font-size: 14px;
  line-height: 22px;
  text-align: start;
  color: #585858;
}

.GenericDetail .title {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #212529;
}


.GenericDetail .icon {
  font-size: 16px !important;
  color: #05BEC0;
}

.GenericDetail .valueContainer {
  overflow: hidden;
  display: inline-block;
  margin-bottom: -7px;
}

.GenericDetail .valueContainer .value {
  display: inline-block;
  animation: loadingAfter 10s infinite ease-in-out;
}


.GenericDetail .showMoreText {
  display: inline;

}

.GenericDetail .showMoreText span {
  white-space: initial;
}

.GenericDetail .showMoreText span:last-child {
  //margin-right: 4px;
}


@keyframes loadingAfter {
  0% {
    transform: translateX(-10%);
  }
  50% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-10%);
  }
}
