.VoucherImageSquare {
  color: #F28F17 !important;
}

.VoucherImageSquarePlane {
  background-image: url(/assets/img/genericPlane2.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.VoucherImageSquareHotel {
  background-image: url(/assets/img/genericHotel2.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.VoucherImageSquareCruise {
  background-image: url(/assets/img/genericCruise.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}

.VoucherImageSquareMap {
  background-image: url(/assets/img/genericMap.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}


.VoucherImageSquare .iconContainerClassName {
  background-color: #F28F17 !important;
}

.VoucherImageSquare :global(.noLogo) {
  display: block !important;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-image: url(/assets/img/no-logo-tickets.png);
}

.VoucherImageSquare .iconContainerClassName svg {
  position: relative;
  left: -3px;
  top: 3px;
}


