.HotelLocationCard :global(.MuiAccordionDetails-root) {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex-wrap: nowrap;
}

.HotelLocationCard .actions {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 25px;
  align-content: center;
}

.HotelLocationCard .actions :global(.GenericDetail) {
  line-height: 35px;
}


.adStreet {
  display: inline-block;
}

@media (max-width: 575.9px) {
  .showtheway {
    margin-top: 3px;
    margin-bottom: 7px;
    float: unset;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
  }
}
