.prices {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: flex-start;
  width: fit-content;
}

.prices .originalPrice {
  text-decoration-line: line-through;
  color: #878787;
  font-weight: 500;
}

.prices .salePrice {
  color: #F28F17;
  font-weight: bold !important;
}
