.PlusMinus {
  color: #BEBEBE;
  float: left;
  margin-left: 17px;
  margin-top: -4px;
}

.PlusMinus svg {
  cursor: pointer;
  width: 26px;
  height: 26px;
}

.PlusMinus .value {
  min-width: 56px;
  display: inline-block;
  text-align: center;
  color: #323232;
}
