.DenyButtonPopOverContainer.popover {
  max-width: fit-content;
}


.actionButtons:global(.MuiButtonBase-root.MuiButton-root) {
  display: inline-block;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  background-color: #05BEC0;
  float: left;
  margin-bottom: 0;
  box-shadow: none !important;

}


.disapproveButton:global(.MuiButtonBase-root.MuiButton-root) {
  display: inline-block;
  min-width: 100px;
  border-radius: 52px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  align-items: center;
  text-align: center;
  float: left;
  margin-bottom: 0;
  box-shadow: none !important;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  color: #F22417;
  background-color: #ffffff !important;
  border-style: solid;
  border-width: 1px;
  padding-top: 7px;
  padding-bottom: 7px;
}

.disapproveButton:global(.MuiButtonBase-root.MuiButton-root):hover {
  background-color: #facece !important;
}


@media (max-width: 260.9px) {
  .actionButtons:global(.MuiButtonBase-root.MuiButton-root) {
    font-size: 16px !important;
    min-width: 60px;
  }
}
