.planeLine {
  position: absolute;
  width: 100%;
  top: 58px;
  right: 10px;
}


:global(.mobile) .planeLine {
  top: 44px;
  width: 80%;
  right: 18%;
}

.planeLine .locationLine {
  width: 38%;
  height: 2px;
  margin-left: auto;
  margin-right: auto;
}

.planeLine .planeIcon {
  height: 25px;
  width: 25px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: -13px;
}

.planeLine .planeIcon.flip {
  -moz-transform: scale(-1, -1);
  -o-transform: scale(-1, -1);
  -webkit-transform: scale(-1, -1);
  transform: scale(-1, -1);
}

.planeLine .hours {
  width: 100%;
  display: block;
  font-weight: 400;
  font-size: 15px;
  text-align: center;
}

:global(.mobile) .hours {
  margin-top: -45px;
}
