.shareButton {
  color: #05BEC0 !important;
  margin-top: -10px !important;
  margin-right: 15px !important;
  float: left;
  border-radius: 5px !important;
}


.shareButton:focus {
  outline: none;
}


.shareButton span {
  font-size: 1rem;
}

@media (max-width: 767.9px) {
  .shareButton {
    float: left;
    margin: 0 !important;

  }

  .shareButton span {
    font-size: 4vw;
  }
}
