.GenericImageSquare {
  display: table;
  border-style: solid;
  border-right: 0px;
  border-top: 0px;
  border-bottom: 0px;
  width: 202px;
  height: 142px;
  object-fit: initial !important;
  border-width: 4px;
  background-position: 0 0;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  // background-image: url(/assets/img/genericPlane.png);
  // color: #05BEC0;
}

.GenericImageSquare .iconContainerClassName,
.GenericImageSquare .iconContainerClassName .iconClassName {
  width: 34px !important;
  height: 19px !important;
  border-radius: 0 !important;
  border-top-right-radius: 4px !important;

}

.GenericImageSquare .iconContainerClassName {
  margin-right: 0px;
  top: 0px;
  margin-top: 0px;

}

.GenericImageSquare .iconContainerClassName .iconClassName {
  padding: 2px !important;
  background-color: transparent !important;
  margin-top: -6px;


  //box-shadow: 0px 2px 3px -1px black;
}

.GenericImageSquare.loading {
  border-color: #118f9026;
}

.GenericImageSquare.loading .iconContainerClassName .iconClassName {
  margin-top: 0;
  background-color: #05bec085 !important;
}

