.Arrows {
  position: absolute;
  width: 100%;
  top: 44%;
  z-index: 2;
  pointer-events: none;
}

.Arrows svg {
  width: 48px;
  height: 48px;
  color: #BEBEBE;
  cursor: pointer;
  pointer-events: initial;
}

.Arrows svg.right {
  float: right;
}

.Arrows svg.left {
  float: left;
}

