.AdPurchase {
  margin-top: 5px;
}

.purchaseButton,
.AdPurchase :global(.MuiSkeleton-root) {
  border-radius: 52px;
  border-color: #05BEC0;
  border-width: 1px;
  border-style: solid;
  font-weight: bold;

  box-sizing: border-box;
  text-align: center;
  background-color: #05BEC0;
  color: white;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  line-height: 23px;
  padding: 10px 30px;
  width: fit-content;
  width: -moz-max-content;
}

.AdPurchase :global(.MuiSkeleton-root) {
  width: 141.17px;
  height: 44.86px;
}


.purchaseButton:hover {
  color: white;
}

@media (max-width: 768px) {
  .purchaseButton,
  .AdPurchase :global(.MuiSkeleton-root) {
    padding-left: 35%;
    padding-right: 35%;
    padding-top: 15px;
    padding-bottom: 15px;
  }
}


@media (max-width: 342px) {
  .purchaseButton,
  .AdPurchase :global(.MuiSkeleton-root) {
    padding-left: 20vw;
    padding-right: 20vw;
  }
}
