.mobileAdStructure {
  box-shadow: none !important;
  // padding-bottom: 35px !important;
}

.mobileAdStructure .topTitle {
  max-width: 86%;
}

.mobileAdStructure .prices {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: space-around;
}

.mobileAdStructure .control {
  position: absolute;
  right: 0;
  bottom: 0px;
  width: 100%;
  margin: 0 !important;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  flex-direction: row;
  padding-right: 20px;
  padding-left: 20px;
  padding-top: 16px;
  border-width: 0;
  border-top: 1px;
  border-color: #D6D6D6;
  border-style: solid;
}

.mobileAdStructure .control :global(.circleButton) {
  display: inline-block;
  margin-left: 30px;
  min-width: 24px;
}


.mobileAdStructure .topTab {
  position: initial;
  width: 100%;
}

.mobileAdStructure .GenericImageSquare, {
  border-width: 0;
  width: 100% !important;
  height: 37vw !important;
  min-height: 178px !important;
  background-position: 50% !important;
}

.mobileAdStructure :global(.LogoSquare) {
  margin-top: 36px;
}

.mobileAdStructure .infoSquare {
  width: 100%;
  height: 100%;
  background-color: rgba(15, 15, 15, 0.76);
  padding: 36px;
  padding-right: 18px;
  text-align: start !important;
  direction: rtl;
}

.mobileAdStructure :global(.image-gallery-slides) .infoSquare {
  position: absolute;
  z-index: 3;
}

.mobileAdStructure .infoSquare span {
  color: white;
}

.mobileAdStructure :global(.MuiCardActions-root) {
  padding-bottom: 30px;
}
