.conclusionCardContainer {
  box-shadow: none;
  margin-bottom: 35px;
}

.conclusionCardContainer :global(.card-body).conclusionCardFormTop,
.conclusionCardContainer :global(.MuiSkeleton-root).conclusionCardFormTop {
  background-color: #FFFFFF !important;
  color: #585858;
  padding-bottom: 32px;
  padding-top: 0px;
}

.conclusionCardContainer .conclusionCardFormTop:global(.card-body) {

}

.conclusionCardContainer :global(.card-footer) .conclusionCardFormBottom {
  background-color: #F28F17 !important;
  border-radius: 52px;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  margin-top: 10px;
  margin-bottom: 10px;
}


.conclusionCardContainer .conclusion {
  color: white;

}

.conclusionCardContainer :global(.card-footer),
.conclusionCardContainer :global(.MuiSkeleton-root).conclusionCardFormBottom {
  background-color: #E3F1F1;
  border-top: 0;
  padding-bottom: 0px;
}

.topPoints {
  display: none;
}

@media (max-width: 767px) {
  .topPoints {
    display: block;
  }
  .conclusionCardContainer :global(.card-body).conclusionCardFormTop,
  .conclusionCardContainer :global(.MuiSkeleton-root).conclusionCardFormTop,
  .conclusionCardContainer .conclusion {
    background-color: #E3F1F1 !important;
  }
}
