.adControlContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 16px;
  padding-bottom: 16px;
  width: 44px;
  height: 142px;
  margin-top: -39px;
  margin-left: -10px;
}

.adControlContainer svg {
  width: 24px !important;
  height: 24px !important;
}


@media (max-width: 1102.5px) {
  .adControlContainer {
    min-width: 132px;
    width: fit-content;
    height: fit-content;
    display: block;
    padding: 0;
    margin-top: -33px;
    margin-left: 40px;
    float: left;
  }
}
